import flatpickr from 'flatpickr';
import 'flatpickr/dist/flatpickr.min.css';

const startBtn = document.querySelector(`[data-start]`);
const timerElem = document.querySelector(`.timer`);
const inputElem = document.querySelector(`#datetime-picker`);

const options = {
  enableTime: true,
  time_24hr: true,
  defaultDate: new Date(),
  minuteIncrement: 1,
  onClose(selectedDates) {
    let selectedDate = selectedDates[0];
    if (selectedDate <= new Date()) {
      iziToast.error({
        title: 'Error',
        message: 'Please choose a date in the future',
        position: 'topRight',
      });
      startBtn.disabled = true;
    } else {
      userSelectedDate = selectedDate;
      startBtn.disabled = false;
    }
  },
};

flatpickr(`#datetime-picker`, options);

let intervalId;
let userSelectedDate;

const daysElem = document.querySelector('[data-days]');
const hoursElem = document.querySelector('[data-hours]');
const minutesElem = document.querySelector('[data-minutes]');
const secondsElem = document.querySelector('[data-seconds]');

startBtn.addEventListener(`click`, () => {
  if (!userSelectedDate) return;

  startBtn.disabled = true;
  inputElem.disabled = true;

  intervalId = setInterval(() => {
    const currentTime = new Date();
    const diffMS = userSelectedDate - currentTime;

    if (diffMS <= 0) {
      clearInterval(intervalId);
      updateTimer(0, 0, 0, 0);
      inputElem.disabled = false;
      return;
    }
    const { days, hours, minutes, seconds } = convertMs(diffMS);
    updateTimer(days, hours, minutes, seconds);
  }, 1000);
});

function updateTimer(days, hours, minutes, seconds) {
  daysElem.textContent = days;
  hoursElem.textContent = addLeadingZero(hours);
  minutesElem.textContent = addLeadingZero(minutes);
  secondsElem.textContent = addLeadingZero(seconds);
}

function convertMs(ms) {
  // Number of milliseconds per unit of time
  const second = 1000;
  const minute = second * 60;
  const hour = minute * 60;
  const day = hour * 24;

  // Remaining days
  const days = Math.floor(ms / day);
  // Remaining hours
  const hours = Math.floor((ms % day) / hour);
  // Remaining minutes
  const minutes = Math.floor(((ms % day) % hour) / minute);
  // Remaining seconds
  const seconds = Math.floor((((ms % day) % hour) % minute) / second);

  return { days, hours, minutes, seconds };
}

console.log(convertMs(2000)); // {days: 0, hours: 0, minutes: 0, seconds: 2}
console.log(convertMs(140000)); // {days: 0, hours: 0, minutes: 2, seconds: 20}
console.log(convertMs(24140000)); // {days: 0, hours: 6 minutes: 42, seconds: 20}

import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

function addLeadingZero(value) {
  return String(value).padStart(2, '0');
}
